import React from 'react'
import styled from 'styled-components'
import tile from '../../images/tile.jpg'
import Header from '../Header'
import ProductsSection from '../ProductsSection'
import banner from '../../images/banner.jpg'
import BeenhereIcon from '@material-ui/icons/Beenhere';

export default function Productspage() {

    return (
        <Container>
            <Header title="Produse"/>
            <Wrapper>
                <Banner>
                    <TextWrapper>
                        <p><BeenhereIcon/> Materiale calitate superioară</p>
                        <p><BeenhereIcon/> Fabricate manual</p>
                        <p><BeenhereIcon/> Discount 10% la fiecare mai mult de 10 produse</p>
                    </TextWrapper>
                    <TextWrapper>
                        <p><BeenhereIcon/> Livare gratuită la o comandă de peste 50 produse</p>
                        <p><BeenhereIcon/> Verificare la livrare</p>
                        <p><BeenhereIcon/> Returnare produs până în maxim 7 zile</p>
                    </TextWrapper>
                </Banner>
                <ProductsSection darkBg={false} type="macelar"/>
                <ProductsSection darkBg={true} type="chef"/>
                <ProductsSection darkBg={false} type="tocare"/>
                <ProductsSection darkBg={true} type="paine"/>
                <ProductsSection darkBg={false} type="peste"/>
                <ProductsSection darkBg={true} type="filat"/>
            </Wrapper>
        </Container>
    )
}


const Container = styled.article`
    background:  url(${tile}) no-repeat top center / contain;
`
const Wrapper = styled.div`
    background-image: linear-gradient(74deg, rgba(236, 236, 236,0.02) 0%, rgba(236, 236, 236,0.02) 13%,transparent 13%, transparent 64%,rgba(55, 55, 55,0.02) 64%, rgba(55, 55, 55,0.02) 71%,rgba(239, 239, 239,0.02) 71%, rgba(239, 239, 239,0.02) 100%),linear-gradient(170deg, rgba(8, 8, 8,0.02) 0%, rgba(8, 8, 8,0.02) 1%,transparent 1%, transparent 60%,rgba(9, 9, 9,0.02) 60%, rgba(9, 9, 9,0.02) 80%,rgba(198, 198, 198,0.02) 80%, rgba(198, 198, 198,0.02) 100%),linear-gradient(118deg, rgba(134, 134, 134,0.02) 0%, rgba(134, 134, 134,0.02) 30%,transparent 30%, transparent 43%,rgba(85, 85, 85,0.02) 43%, rgba(85, 85, 85,0.02) 47%,rgba(103, 103, 103,0.02) 47%, rgba(103, 103, 103,0.02) 100%),linear-gradient(249deg, rgba(178, 178, 178,0.02) 0%, rgba(178, 178, 178,0.02) 8%,transparent 8%, transparent 47%,rgba(161, 161, 161,0.02) 47%, rgba(161, 161, 161,0.02) 61%,rgba(19, 19, 19,0.02) 61%, rgba(19, 19, 19,0.02) 100%),linear-gradient(90deg, rgb(255,255,255),rgb(255,255,255));
    background-color: white;
    border-top-left-radius: 20vw;
    border-bottom-right-radius: 20vw;
    overflow: hidden;
`
const Banner = styled.div`
    background: url(${banner}) no-repeat center center / cover;
    min-height: 30vh;
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and (max-width: 600px) {
        flex-direction: column;
    }
`
const TextWrapper = styled.div`
    margin: 10px 10px 10px 50px;
    display: flex;
    flex-direction: column;
    justify-items: baseline;
    >p {
        font-size: clamp(0.8rem, 4vw, 1rem);
        margin: 20px 0;
        display: flex;
        align-items: center;
        >svg {
            color: #009200;
        }
        @media screen and (max-width: 600px) {
            margin: 10px 0;
        }
    }
`