import React from 'react'
import styled from 'styled-components'
import PersonPinCircleIcon from '@material-ui/icons/PersonPinCircle'
import PeopleIcon from '@material-ui/icons/People'
import LocalDiningIcon from '@material-ui/icons/LocalDining'
import LocalDrinkIcon from '@material-ui/icons/LocalDrink';
import { useState } from 'react'
import NumberCounter from './NumberCounter'

export default function Counters() {
    const [clients, setClients] = useState(0)
    const [products, setProducts] = useState(0)
    const [cities, setCities] = useState(0)
    const [beers, setBeers] = useState(0)

    

    return (
        <Container>
            <NumberCounter title="CLIENȚI" setNumber={setClients} icon={<PeopleIcon/>} number={clients} maxNr={57}/>
            <NumberCounter title="PRODUSE VÂNDUTE" setNumber={setProducts} icon={<LocalDiningIcon/>} number={products} maxNr={1544}/>
            <NumberCounter title="ORAȘE" setNumber={setCities} icon={<PersonPinCircleIcon/>} number={cities} maxNr={23}/>
            <NumberCounter title="BERI SRVITE" setNumber={setBeers} icon={<LocalDrinkIcon/>} number={beers} maxNr={163}/>
        </Container>
    )
}



const Container = styled.aside`
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 1000px;
    margin: 0 auto;
    font-family:  'Segoe UI', 'Roboto', 'Oxygen', sans-serif;
`

