import styled from 'styled-components';
import { Link } from 'react-router-dom';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import YouTubeIcon from '@material-ui/icons/YouTube';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import InstagramIcon from '@material-ui/icons/Instagram';

export default function Footer() {
    return (
        <FooterContainer>
            <FooterWrapper>
                <FooterLinksContainer>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>About Us</FooterLinkTitle>
                            <FooterLink to="signin">How it works</FooterLink>
                            <FooterLink to="signin">Testimonials</FooterLink>
                            <FooterLink to="signin">Carrers</FooterLink>
                            <FooterLink to="signin">Investors</FooterLink>
                            <FooterLink to="signin">Terms of Service</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle>Contact Us</FooterLinkTitle>
                            <FooterLink to="">Contact</FooterLink>
                            <FooterLink to="">Support</FooterLink>
                            <FooterLink to="">Destinations</FooterLink>
                            <FooterLink to="">Sponsorship</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>Videos</FooterLinkTitle>
                            <FooterLink to="">Submit Video</FooterLink>
                            <FooterLink to="">Ambassadors</FooterLink>
                            <FooterLink to="">Agency</FooterLink>
                            <FooterLink to="">Influencer</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle>Social Media</FooterLinkTitle>
                            <FooterLink to="">Instagram</FooterLink>
                            <FooterLink to="">Facebook</FooterLink>
                            <FooterLink to="">Youtube</FooterLink>
                            <FooterLink to="">Twitter</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                </FooterLinksContainer>
                <SocialMedia>
                    <SocialMediaWrapper>
                        <SocialLogo onClick={() => window.scrollTo(0, 0)} to="">Knives.ug</SocialLogo>
                        <WebsiteRights>Knives.ug &copy;{new Date().getFullYear()} All rights reserved.</WebsiteRights>
                        <SocialIcons>
                            <SocialIconLink to="/" target="_blank" aria-label="Facebook">
                                <FacebookIcon />
                            </SocialIconLink>
                            <SocialIconLink to="/" target="_blank" aria-label="Instagram">
                                <InstagramIcon />
                            </SocialIconLink>
                            <SocialIconLink to="/" target="_blank" aria-label="Youtube">
                                <YouTubeIcon />
                            </SocialIconLink>
                            <SocialIconLink to="/" target="_blank" aria-label="Twitter">
                                <TwitterIcon />
                            </SocialIconLink>
                            <SocialIconLink to="/" target="_blank" aria-label="LinkedIn">
                                <LinkedInIcon />
                            </SocialIconLink>
                        </SocialIcons>
                    </SocialMediaWrapper>
                </SocialMedia>
            </FooterWrapper>
        </FooterContainer>
    )
}






const FooterContainer = styled.footer`
    background-color: #0a0a0a;
    box-shadow: 0 10px 0 #080808;
`
const FooterWrapper = styled.div`
    padding: 48px 24px;
    height: 700px;
    gap: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 1100px;
    margin: 0 auto;
    @media screen and (max-width: 800px) {
        height: 800px;
    }
`
const FooterLinksContainer = styled.div`
    display: flex;
    gap: 5vw;
    justify-content: center;
    border-bottom: 1px solid #303030;
    padding-bottom: 70px;
    @media screen and (max-width: 820px) {
        padding-top: 32px;
    }
`
const FooterLinksWrapper = styled.div`
    display: flex;
    flex: 1;
    gap: 5vw;
    @media screen and (max-width: 820px) {
        flex-direction: column;
    }
`
const FooterLinkItems = styled.div`
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: flex-start;
    margin: 1rem;
    text-align: left;
    
    color: white;
    @media screen and(max-width: 420px) {
        margin: 0;
        padding: 10px;
        width: 100%;
    }
`
const FooterLinkTitle = styled.h1`
    font-size: 14px;
    margin-bottom: 1rem;
`
const FooterLink = styled(Link)`
    color: white;
    text-decoration: none;
    margin-bottom: 0.5rem;
    font-size: 14px;
    white-space: nowrap;
    &:hover {
        color: var(--c1h);
        transition: all 0.3s;
    }
`
const SocialMedia = styled.section`
    max-width: 1000px;
    width: 100%;
`
const SocialMediaWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1100px;
    margin: 40px auto 0 auto;
    @media screen and (max-width: 820px) {
        flex-direction: column;
    }
`
const SocialLogo = styled(Link)`
    color: white;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    font-weight: bold;
`
const WebsiteRights = styled.small`
    color: white;
    margin-bottom: 1rem;
`
const SocialIcons = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
`
const SocialIconLink = styled(Link)`
    color: white;
    font-size: 24px;
`
