import React from 'react'
import styled from 'styled-components'
import img1 from '../images/about-gallery/img1.jpg'
import img2 from '../images/about-gallery/img2.jpg'
import img3 from '../images/about-gallery/img3.jpg'
import img4 from '../images/about-gallery/img4.jpg'
import img5 from '../images/about-gallery/img5.jpg'
import img6 from '../images/about-gallery/img6.jpg'
import img7 from '../images/about-gallery/img7.jpg'
import img8 from '../images/about-gallery/img8.jpg'
import img9 from '../images/about-gallery/img9.jpg'
import img10 from '../images/about-gallery/img10.jpg'
import img11 from '../images/about-gallery/img11.jpg'
import img12 from '../images/about-gallery/img12.jpg'
import img13 from '../images/about-gallery/img13.jpg'
import img14 from '../images/about-gallery/img14.jpg'
import img15 from '../images/about-gallery/img15.jpg'
import img16 from '../images/about-gallery/img16.jpg'
import img17 from '../images/about-gallery/img17.jpg'
import img18 from '../images/about-gallery/img18.jpg'
import img19 from '../images/about-gallery/img19.jpg'
import { useState } from 'react'
import { useRef } from 'react'
import AboutGalleryImage from './AboutGalleryImage'
import { MotionGallery } from './MotionGallery'


export default function AboutGallery() {
    const [image, setImage] = useState(null)
    const gridRef = useRef()
    
    let list = [img1, img2, img3, img4, img5, img6, img7, img8, img9, img10, img11, img12, img13, img14, img15, img16, img17, img18, img19]

    const openModal = (img) => {
        setImage(img)
    }
    const closeModal = e => {
        if (e.target.nodeName !== "SECTION") {
            setImage(null)
        }
    }


    return (
        <Container ref={gridRef}>

            {list && list.map((img, i) => (
                <AboutGalleryImage key={i} img={img} openModal={openModal} />
            ))}
            

            {image && <MotionGallery images={list} image={image} closeModal={closeModal}/>}
        </Container>
    )
}



const Container = styled.aside`
    margin-top: 200px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    grid-auto-rows: 270px;
    gap: 20px;
    position: relative;
`

