import React from 'react'
import styled from 'styled-components'
import { useEffect } from 'react'
import { useRef } from 'react'
import { useState } from 'react'



export default function NumberCounter({title, icon, number, maxNr, setNumber}) {
    const numberRef = useRef()
    const [top, setTop] = useState(false)

    function whenScrolled() {
        if (number < maxNr) {
            let increment = maxNr / 500
            setNumber(Math.ceil(number += increment))
            setTimeout(whenScrolled, 10)
        }
    }
    
    useEffect(() => {
        const updateCounters = () => {
            if (window.pageYOffset < 200) {
                setTop(true)
            }
            if (top && numberRef.current.getBoundingClientRect().y < window.innerHeight) {
                whenScrolled()
            }
        }
        window.addEventListener('scroll', updateCounters)
        
        return () => window.removeEventListener('scroll', updateCounters)

    }, [top])

    return (
        <Wrapper ref={numberRef}>
            {icon}
            <Numbers>{(number).toLocaleString('ro')}+</Numbers>
            <Span>{title}</Span>
        </Wrapper>
    )
}

const Wrapper = styled.div`
    flex: 1 1 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin: 30px 50px;
    >svg {
        font-size: 2rem;
    }
`
const Numbers = styled.p`
    font-size: clamp(1.6rem, 5vw, 3rem);
    margin-top: 10px;
`
const Span = styled.span`

`