import { TableHead } from '@material-ui/core'
import { TableRow } from '@material-ui/core'
import { TableBody } from '@material-ui/core'
import { Paper } from '@material-ui/core'
import { Table } from '@material-ui/core'
import { TableCell } from '@material-ui/core'
import { TableContainer } from '@material-ui/core'
import React from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import styled from 'styled-components'
import { db, st } from './firebase'
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import { Fab } from '@material-ui/core'
import {motion} from 'framer-motion'
import Pagination from '@material-ui/lab/Pagination';



export default function ProductTable({setTotal, added, setAdded}) {
    const [items, setItems] = useState([])
    const [page, setPage] = useState(1)
    const itemsPerPage = 10
    const noOfPages = Math.ceil(items.length / itemsPerPage)
    

    const handleChange = (e, value) => {
        setPage(value)
    }
    
    useEffect(() => {
        db.collection('knives').orderBy('timestamp', 'desc').onSnapshot(snap => (
            setTotal(snap.size),
            setItems(snap.docs.map(doc => (
                {
                    id: doc.id,
                    data: doc.data()
                }
            )))
        ))
    }, [])


    const indexOfLastItem = page * itemsPerPage
    const indexOfFirstItem = indexOfLastItem - itemsPerPage
    const currentItems = items.slice(indexOfFirstItem, indexOfLastItem)


    const handleDelete = ({id, name, img}) => {
        if (window.confirm(`Sigur ștergi produsul "${name}"?`) === true) {
            db.collection('knives').doc(id).delete()
            st.refFromURL(img).delete()
        }
    }

    useEffect(() => {
        setPage(1)
        setAdded(false)
        return
    }, [added])
    
    return (
        <Container>
            <TableContainer component={Paper}>
                <Table style={{minWidth: 650}} aria-label="simple table"  size="small">
                    <TableHead style={{backgroundColor: "#505050", position: 'sticky', top: 0}}>
                        <TableRow>
                            <HeadCell>Index</HeadCell>
                            <HeadCell>Imagine</HeadCell>
                            <HeadCell align="left">Nume Produs</HeadCell>
                            <HeadCell align="right">ID</HeadCell>
                            <HeadCell align="left">Descriere</HeadCell>
                            <HeadCell align="right">Preț (RON)</HeadCell>
                            <HeadCell align="right">Stoc</HeadCell>
                            <HeadCell align="right">Op1</HeadCell>
                            <HeadCell align="right">Op2</HeadCell>
                            <HeadCell align="right">Op3</HeadCell>
                            <HeadCell align="right">Op4</HeadCell>
                            <HeadCell align="right">Șterge</HeadCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                            {currentItems && currentItems.map(({id, data}, idx) => (
                                <TableRow  key={id} title={new Date(data.timestamp?.toDate()).toString().slice(0, 25)}>
                                    <TableCell align="center">{((page - 1) * 10) + (idx + 1)}</TableCell>
                                    <TableCell component="th" scope="row">
                                        <ProductImage 
                                            src={data.image} 
                                            alt={data.name} 
                                            as={motion.img}
                                            initial={{scale: 0}}
                                            animate={{scale: 1}}
                                            transition={{delay: 0.5}}
                                        />
                                    </TableCell>
                                    <TableCell align="left">{data.name}</TableCell>
                                    <TableCell align="right">{data.id}</TableCell>
                                    <MyTableCell align="left"><p>{data.description}</p></MyTableCell>
                                    <TableCell align="right">{Number(data.price).toLocaleString('ro')}</TableCell>
                                    <TableCell align="right">{data.stock}</TableCell>
                                    <TableCell align="right">{data.option1}</TableCell>
                                    <TableCell align="right">{data.option2}</TableCell>
                                    <TableCell align="right">{data.option3}</TableCell>
                                    <TableCell align="right">{data.option4}</TableCell>
                                    <TableCell align="right" onClick={() => handleDelete({id, name: data.name, img: data.image})}>
                                        <Fab size="small" >
                                            <DeleteOutlineIcon color="secondary"/>
                                        </Fab>
                                    </TableCell>
                                </TableRow>
                            ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <InfoText>* Fiecare imagine trebuie să aibă o denumire unică</InfoText>
            <br/>
            <InfoText>** Fiecare produs trebuie să aibă un "Nume" unic</InfoText>
            <br/>
            <InfoText>*** Aceste produse sunt afișate pe pagina principală în secțiunea "CATALOG" și pe pagina "Produse" și sunt grupate pe bază de id</InfoText>
            
            <MyPagination
                onChange={handleChange}
                count={noOfPages}
                page={page}
                defaultPage={page}
                hideNextButton={false}
                hidePrevButton={false}
            />
        </Container>
    )
}



const Container = styled.aside`
    margin: 100px auto;
    max-width: 95%;
    table tbody {
        >:nth-child(even) {
            background-color: #f3f3f3;
        }
    }
`
const HeadCell = styled(TableCell)`
    color: white !important;
`
const ProductImage = styled.img`
    overflow: hidden;
    display: block;
    max-width: 100px;
    max-height: 50px;
`
const MyPagination = styled(Pagination)`
    display: block;
    margin: 0 auto;
    padding-top: 30px;
    display: flex;
    justify-content: center;
`
const InfoText = styled.small`
    color: grey;
`
const MyTableCell = styled(TableCell)`
    >p {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        text-align: justify;
    }
`