import React from 'react'
import styled from 'styled-components'
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { useState } from 'react';
import { useEffect } from 'react';
import { db } from './firebase';
import { Button } from '@material-ui/core';


const SoldProducts = React.memo(() => {
    const [items, setItems] = useState([])


    useEffect(() => {
        db.collection('sold').orderBy('timestamp', 'desc').onSnapshot(snap => (
            setItems(snap.docs.map(doc => (
                {
                    id: doc.id,
                    data: doc.data()
                }
            )))
        ))
    }, [])

    const handleDelivered = (id, delivered) => {
        db.collection('sold').doc(id).update({
            delivered: !delivered 
        })
    }

    const handleDelete = (id) => {
        if (window.confirm(`Sigur ștergi acest client?`) === true) {
            db.collection('sold').doc(id).delete()
        }
    }


    return (
        <Container>
            <Title>De expediat:</Title>

            {items && items.length < 1 ? 
                <h4>Nimic de Livrat</h4> : 
                items.map(({id, data}, idx) => (
                <MyAccordion  key={id} className={data.delivered ? 'marked' : ''}>
                    <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1bh-content"
                    id="panel1bh-header"
                    >
                        <MyTypography className={data.delivered ? 'marked' : ''}>{data.lname} {data.name}</MyTypography>
                        <PriceTypography className={data.delivered ? 'marked' : ''}><i>Suma:</i> <b>{data.total}</b> RON</PriceTypography>
                    </AccordionSummary>
                    <AccordionDetails style={{flexDirection: 'column'}}>
                        <Typography><i>Cumpărat la data:</i> <b>{new Date(data.timestamp?.toDate()).toLocaleString('ro')}</b>
                        </Typography>
                        <Typography><i>Telefon/E-mail:</i> <b>{data.phone} / {data.email}</b></Typography>
                        <Typography><i>Adresa:</i> <b>{data.address}</b></Typography>
                        <Typography><i>Cod Poștal</i> <b>{data.zip}</b></Typography>
                        <Typography><i>Localitatea:</i> <b>{data.city}</b></Typography>
                        <Typography><i>Județul</i> <b>{data.county}</b></Typography>
                        <div>
                            {data.items.map((item, i) => (
                            <div key={i} style={{borderTop: '1px solid lightgray', marginTop: '20px', paddingTop: '10px'}}>
                                <Typography><u>Produs{i + 1}:</u></Typography>
                                <Text><i>Denumire:</i> <b>{item.product}</b></Text>
                                <Text><i>Preț:</i> <b>{item.price}</b> RON</Text>
                                <Text><i>Bucăți:</i> <b>{item.amount}</b></Text>
                            </div>
                        ))}
                        </div>
                        <Buttons>
                            <Button variant="outlined" size="small" onClick={() => handleDelivered(id, data.delivered)}>
                                {data.delivered ? 'Marchează ca nelivrat' : 'Marchează ca livrat'}
                            </Button>
                            <Button variant="contained" color="secondary" disableElevation={true} onClick={() => handleDelete(id)} size="small">
                                Șterge
                            </Button>
                        </Buttons>
                    </AccordionDetails>
                </MyAccordion>
            ))}
        </Container>
    )
})

export default SoldProducts


const Container = styled.div`
    width: 1000px;
    max-width: 96vw;
    margin: 100px auto 0;
`
const Title = styled.h2`
    font-weight: 400;
    margin: 20px 0;
`
const MyAccordion = styled(Accordion)`
    &.marked {
        background-color: grey;
    }
`
const MyTypography = styled(Typography)`
    flex-basis: 50%;
    flex-shrink: 0;
    &.marked {
        color: lightgray;
        text-decoration: line-through;
    }
`
const PriceTypography = styled(Typography)`
    text-align: right;
    width: 100%;
    >b{
        color: var(--c1);
    }
    &.marked {
        color: lightgray;
        text-decoration: line-through;
    }
`
const Text = styled.p`
    margin-left: 30px;
`
const Buttons = styled.div`
    align-self: flex-end;
    >button {
        margin-left: 10px;
    }
`