import { createSlice } from '@reduxjs/toolkit';


export const errorSlice = createSlice({
    name: 'error',
    initialState: {
        selectError: null,
        showError: false
    },
    reducers: {
        selectError: (state, action) => {
            state.selectedError = action.payload;
        },
        showErrorIsTrue: (state) => {
            state.showError = true;
        },
        showErrorIsFalse: (state) => {
            state.showError = false;
        }
    },
});

export const { selectError, showErrorIsTrue, showErrorIsFalse } = errorSlice.actions;

export const selectErrorMessage = (state) => state.error.selectedError;

export const selectShowError = (state) => state.error.showError;

export default errorSlice.reducer;
