import React from 'react';
import { useRef } from 'react';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import styled from 'styled-components';
import { selectError, showErrorIsFalse } from '../features/errorSlice';
import {motion} from 'framer-motion'


export default function TransitionsSnackbar({error}) {
    const elementRef = useRef()
    const dispatch = useDispatch()

         
    useEffect(() => {
        const handleCloseError = (e) => {
            if (e.target !== elementRef.current) {
                dispatch(selectError(null));
                dispatch(showErrorIsFalse())
            } 
        }

        window.addEventListener('click', handleCloseError)
        
        return () =>  window.removeEventListener('click', handleCloseError)
    }, [error, dispatch])
    

    return (
            <Container as={motion.div} animate={{y: 0, x: '-50%'}}  ref={elementRef}>
                {error}
            </Container>
    );
}




const Container = styled.div`
    position: fixed;
    z-index: 20;
    bottom: 20px;
    left: 50%;
    transform:  translateX(-50%);
    background-color: #383838;
    color: white;
    padding: 10px;
    text-align: center;
    box-shadow: 0 3px 5px #000000c3;
    border-radius: 5px;
    max-width: 90vw;
`