import React, { useEffect, useRef } from 'react'
import styled from 'styled-components'
import cover from '../images/cover.jpg'
import overlayVid from '../videos/overlay.mp4'
import { init } from 'ityped'
import { Button } from '@material-ui/core'
import knife1 from '../images/knife1.png'
import { Link } from 'react-router-dom'


export default function WelcomeSection() {
    const textRef = useRef();
    const imgContainerRef = useRef();
    const imgRef = useRef();

    useEffect(() => {
        init(textRef.current, {
            showCursor: true,
            backDelay: 1500,
            strings: ["pescuit", "bucătărie", "vânare", "buzunar", "salvare", "camping"]
        });
        return
    }, []);

    const handleHover = e => {
        let xAxis = (window.innerWidth/1.4  - e.pageX) / 20
        let yAxis = (window.innerHeight/2  - e.pageY) / 20
        imgContainerRef.current.style.transform = `rotateX(${yAxis}deg) rotateY(${-xAxis}deg)`
    }
    const handleLeave = () => {
        imgContainerRef.current.style.transform = 'rotateX(0deg) rotateY(0deg)'
        imgContainerRef.current.style.transition = 'all 0.5s ease'
        imgRef.current.style.transform = 'translateZ(0px) rotateZ(0deg)'
    }
    const handleEnter = () => {
        imgContainerRef.current.style.transition = 'none'
        imgRef.current.style.transform = 'translateZ(200px) rotateZ(-90deg)'
    }

    return (
        <Container id="welcome">
            <Video src={overlayVid} type="video/mp4" autoPlay muted loop></Video>
            <Wrapper>
                <LeftSide>
                    <H3>Transformăm bucătăria intr-un loc de joacă</H3>
                    <H1>Cuțite premium fabricate de către cei mai buni meșteri</H1>
                    <P>Producem cele mai bune și mai durabile cuțite pentru <SpanContainer><Span ref={textRef}></Span></SpanContainer>  dintr-o gamă largă de materiale precum titaniu, damasc, fibră de carbon. Meșterii noștri sunt specialiști atestați ce produc cele mai inovative/tradiționale cuțite de pe piața din România</P>
                    <Link to="/cos"><Button variant="contained">Cumpără</Button></Link>
                </LeftSide>

                <RightSide onMouseMove={handleHover} onMouseLeave={handleLeave} onMouseEnter={handleEnter}>
                    <Knife ref={imgContainerRef}>
                        <Img ref={imgRef} src={knife1} alt="knife"/>
                    </Knife>
                </RightSide>
            </Wrapper>
        </Container>
    )
}

const Container = styled.section`
    height: 100vh;
    background: linear-gradient(to top, black, #0000009d 5%), url(${cover}) no-repeat center / cover;
    position: relative;
`
const Video = styled.video`
    width: 100%;
    height: 100%;
    object-fit: cover;
    mix-blend-mode: screen;
    position: absolute;
    opacity: 0.5;
`
const Wrapper = styled.article`
    height: 100%;
    display: flex;
    @media screen and (max-width: 900px) {
        flex-direction: column-reverse;
    }
`
const LeftSide = styled.div`
    flex: 1;
    color: white;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-left: 10vw;
    padding-right: 5vw;
    >a {
        text-decoration: none;
        >button {
            color: white;
            background-color: var(--c1);
            width: 200px;
            height: 40px;
            border-radius: 50px;
            position: relative;
            &:hover {
                background-color: var(--c1h);
                &::after {
                    content: '►';
                    animation: none;
                    right: 30px;
                }
            }
            &::after {
                content: '▷';
                position: absolute;
                top: 50%;
                right: 50px;
                transform:translateY(-50%);
                animation: arrow 2s infinite;
                @keyframes arrow {
                    to {
                        right: 20px;
                    }
                }
            }
        }
    }
`
const H3 = styled.h3`
    font-size: clamp(1rem, 2vw, 1.6rem);
    font-family: 'Big Shoulders Display', cursive;
    line-height: 2;
    font-weight: 500;
    color: lightgray;
`
const H1 = styled.h1`
    font-size: clamp(2rem, 4vw, 5rem);
    font-family: 'Big Shoulders Display', cursive;
    line-height: 1;
    position: relative;
    margin-bottom: 20px;
    &::after {
        content: '';
        position: absolute;
        bottom: -20px;
        left: -20%;
        height: 2px;
        width: 70%;
        background-color: lightgray;
    }
`
const P = styled.p`
    margin-top: 20px;
    font-size: clamp(0.9rem, 1.5vw, 1.2rem);
    color: lightgray;
    min-height: 150px;
    .ityped-cursor {
        color: black;
        font-size: 1.2rem;
        opacity: 1;
        -webkit-animation: blink 0.3s infinite;
        -moz-animation: blink 0.3s infinite;
        animation: blink 0.3s infinite;
        animation-direction: alternate;
    }
    @keyframes blink {
        100% {
            opacity: 0;
        }
    }
`
const SpanContainer = styled.span`
    min-width: 80px;
    display: inline-block;
    font-weight: 700;
    color: var(--c1);
`
const Span = styled.span`

`
const RightSide = styled.div`
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    perspective: 1000px;
    transform-origin: center center;
    @media screen and (max-width: 900px) {
        display: none;
    }
`
const Knife = styled.div`
    min-width: 250px;
    min-height: 250px;
    max-width: 20vw;
    max-height: 20vw;
    border-radius: 50%;
    background-image: linear-gradient(45deg, #0000001d, #000000d6);
    transform-style: preserve-3d;
`
const Img = styled.img`
    opacity: 1;
    width: 100%;
    object-fit: contain;
    transition: transform 0.5s ease;
`