import React from 'react'
import styled from 'styled-components'
import { useState } from 'react'
import { useEffect } from 'react'
import KnifeTypes from './KnifeTypes'
import arrow from '../images/left-arrow.svg'
import { Link } from 'react-router-dom'
import { db } from './firebase'
import noImage from '../images/noimage.jpg'



const ShopSection = React.memo(() => {
    const [selected, setSelected] = useState('tocare')
    const [items, setItems] = useState([])

    useEffect(() => {
        db.collection('knives').where("id", "==", `${selected}`).onSnapshot(snap => (
            setItems(snap.docs.map(doc => (
                {
                    id: doc.id,
                    data: doc.data()
                }
            )))
        ))
    }, [selected])

    
    const list = [
        {
            id: 'tocare',
            title: 'Tocare'
        },
        {
            id: 'paine',
            title: 'Pâine'
        },
        {
            id: 'chef',
            title: 'Chef'
        },
        {
            id: 'macelar',
            title: 'Măcelar'
        },
        {
            id: 'filat',
            title: 'Filat'
        },
        {
            id: 'peste',
            title: 'Pește'
        }
    ]

    const handleNoImage = e => {
        e.target.src = noImage;
    }

    return (
        <Container>

            <Wrapper>
                <TopSide>
                    <H1>CATALOG</H1>
                    <Ul>
                        {
                            list.map((knife, i) => (
                                
                                <KnifeTypes key={i} title={knife.title} active={selected === knife.id} id={knife.id} setSelected={setSelected}/>
                            ))
                        }
                    </Ul>
                </TopSide>

                <BottomSide>
                    <Description>Indiferent ce tipuri de cuțite folosești, esențial este să le manevrezi cu atenție și să le depozitezi corect, într-un suport special de lemn, unde pot fi ușor de găsit, niciodată aruncate într-un sertar, pentru că riști să te tai.</Description>

                    <ProductContainer>
                        {
                           items && items.map(({id, data}) => (
                                <MyLink to="/produse" onClick={() => window.scrollTo(0, 0)} key={id}>
                                    <KnifeCard>
                                        <KnifeImg onError={handleNoImage} src={data.image}  alt=""/>
                                        <TextContainer>
                                            <KnifeTitle>{data.name}</KnifeTitle>
                                            <KnifeDescription>{data.description}</KnifeDescription>
                                        </TextContainer>
                                    </KnifeCard>
                                </MyLink>
                            ))
                        }
                    </ProductContainer>
                </BottomSide>
            </Wrapper>

            <Arrow src={arrow} alt=""/>
        </Container>
    )
})
export default ShopSection



const Container = styled.section`
    min-height: 100vh;
    position: relative;
    background-image: linear-gradient(22.5deg, rgba(67, 67, 67, 0.02) 0%, rgba(67, 67, 67, 0.02) 29%,rgba(47, 47, 47, 0.02) 29%, rgba(47, 47, 47, 0.02) 37%,rgba(23, 23, 23, 0.02) 37%, rgba(23, 23, 23, 0.02) 55%,rgba(182, 182, 182, 0.02) 55%, rgba(182, 182, 182, 0.02) 69%,rgba(27, 27, 27, 0.02) 69%, rgba(27, 27, 27, 0.02) 71%,rgba(250, 250, 250, 0.02) 71%, rgba(250, 250, 250, 0.02) 100%),linear-gradient(67.5deg, rgba(117, 117, 117, 0.02) 0%, rgba(117, 117, 117, 0.02) 14%,rgba(199, 199, 199, 0.02) 14%, rgba(199, 199, 199, 0.02) 40%,rgba(33, 33, 33, 0.02) 40%, rgba(33, 33, 33, 0.02) 48%,rgba(135, 135, 135, 0.02) 48%, rgba(135, 135, 135, 0.02) 60%,rgba(148, 148, 148, 0.02) 60%, rgba(148, 148, 148, 0.02) 95%,rgba(53, 53, 53, 0.02) 95%, rgba(53, 53, 53, 0.02) 100%),linear-gradient(315deg, rgba(190, 190, 190, 0.02) 0%, rgba(190, 190, 190, 0.02) 6%,rgba(251, 251, 251, 0.02) 6%, rgba(251, 251, 251, 0.02) 18%,rgba(2, 2, 2, 0.02) 18%, rgba(2, 2, 2, 0.02) 27%,rgba(253, 253, 253, 0.02) 27%, rgba(253, 253, 253, 0.02) 49%,rgba(128, 128, 128, 0.02) 49%, rgba(128, 128, 128, 0.02) 76%,rgba(150, 150, 150, 0.02) 76%, rgba(150, 150, 150, 0.02) 100%),linear-gradient(270deg, #ffffff,#ffffff);
    &::after {
        content: '';
        position: absolute;
        bottom: 100px;
        right: 10%;
        width: 400px;
        height: 400px;
        max-width: 50vw;
        max-height: 100vw;
        background-color: #0000007b;
        pointer-events: none;
    }
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 400px;
        height: 200px;
        max-width: 50vw;
        max-height: 50vw;
        background-color: #00000040;
        pointer-events: none;
    }
`
const Wrapper = styled.div`
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1;
`
const TopSide = styled.div`
    display: flex;
    align-items: flex-end;
    justify-content: space-evenly;
    flex: 1;
    @media screen and (max-width: 900px) {
        flex-direction: column;
    }
`
const H1 = styled.h1`
    margin: 50px;
    font-size: clamp(2rem, 5vw, 3rem);
    font-family: 'Big Shoulders Display', cursive;
`
const Ul = styled.ul`
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 30px;
`
const BottomSide = styled.div`
    max-height: 50%;
    display: flex;
    flex: 1;
    align-items: flex-end;
    justify-content: flex-end;
    @media screen and (max-width: 900px) {
        flex-direction: column;
    }
`
const Description = styled.p`
    align-self: center;
    text-align: justify;
    margin: 50px;
    max-width: 400px;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        bottom: -20px;
        width: 20%;
        right: 0;
        height: 2px;
        background-color: var(--c1);
        animation: underln 6s infinite alternate linear;
        @keyframes underln {
            30% {
                transform: translateX(20%);
                width: 50%;
            }
            100% {
                transform: translateX(20%);
                width: 50%;
            }
        }
    }
`
const ProductContainer = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    >a:nth-child(even) img {
        filter: brightness(60%);
    }
    >a:nth-child(odd) img {
        filter: brightness(80%);
    }
    >a:nth-child(even) {
        margin-right: 10%;
        margin-top: -40px;
    }
    @media screen and (min-width: 900px) {
        width: 60%;
    }
`
const MyLink = styled(Link)`
    flex: 0 1 300px;
    height: 250px;
    color: white;
    cursor: pointer;
    `
const KnifeCard = styled.div`
    position: relative;
    overflow: hidden;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 1s;
    box-shadow: inset 0px 0px 10px white;
    &:hover {
        background-color: var(--c1h);
        img {
            opacity: 0.2;
            z-index: 0;
        }
    }
`
const KnifeImg = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
`
const TextContainer = styled.div`
    position: absolute;
`
const KnifeTitle = styled.h3`
    font-size: 1.4rem;
    text-align: center;
`
const KnifeDescription = styled.p`
    font-size: 0.9rem;
    text-align: center;
`
const Arrow = styled.img`
    position: absolute;
    bottom: -40px;
    left: -40px;
    width: 300px;
    max-width: 50%;
    transform: rotate(-45deg);
    z-index: 0;
    opacity: 0.3;
`