import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Navbar from './components/Navbar';
import Homepage from './components/pages/Homepage';
import Footer from './components/Footer';
import Aboutpage from './components/pages/Aboutpage';
import Contactpage from './components/pages/Contactpage';
import Productspage from './components/pages/Productspage';
import Cartpage from './components/pages/Cartpage';
import Loginpage from './components/pages/Loginpage';
import { useSelector } from 'react-redux';
import { intoCart, intoFav, login, selectAdmin, selectUser } from './features/userSlice';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { auth, db } from './components/firebase';
import Adminpage from './components/pages/Adminpage';
import { useState } from 'react';
import PopupMessage from './components/PopupMessage'
import { selectErrorMessage, selectShowError } from './features/errorSlice';
import firebase from 'firebase'
import Successpage from './components/pages/stripe-checkout/Successpage';
// import Shipmentpage from './components/pages/Shipmentpage';



function App() {
  const user = useSelector(selectUser)
  const admin = useSelector(selectAdmin)
  const dispatch = useDispatch()
  const [owner, setOwner] = useState(false)
  const showErrorIsOpen = useSelector(selectShowError)
  const error = useSelector(selectErrorMessage)


  useEffect(() => {
    auth.onAuthStateChanged(user => {
      if (user) {
        dispatch(login({
          displayName: user.displayName,
          email: user.email,
          photoUrl: user.photoURL
        }))
        if (user.email === admin) {
          setOwner(true)
        } else {
          const userDoc = user?.email
          db.collection('users').doc(userDoc).set({
            name: user.displayName,
            email: user.email,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
          })
          const arrayOfCart = []
          db.collection('users').doc(userDoc).collection('cart').get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              arrayOfCart.push(doc.data().name)
            })
            dispatch(intoCart([...arrayOfCart]))
          })
          const arrayOfFav = []
          db.collection('users').doc(userDoc).collection('fav').get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              arrayOfFav.push(doc.data().name)
            })
            dispatch(intoFav([...arrayOfFav]))
          })
        }
      } else {
        dispatch(intoCart([]))
        dispatch(intoFav([]))
      }
    })
  }, [dispatch, admin])



  return (
    <div>
      <Router basename="/stripe-test">
        <Navbar owner={owner} setOwner={setOwner} />
        < Switch >
          <Route path="/despre" exact component={Aboutpage} />
          <Route path="/depozit" exact component={owner ? Adminpage : user && !owner ? Homepage : Loginpage} />
          <Route path="/" exact component={Homepage} />
          <Route path="/contact" exact component={Contactpage} />
          <Route path="/produse" exact component={Productspage} />

          <Route path="/cos" exact>
            {!owner && user ?
              <Cartpage owner={owner} setOwner={setOwner} />
              :
              <Homepage />
            }
          </Route>

          <Route path="/success">
            <Successpage />
          </Route>
          {/* <Route path="/comanda" exact>
            {
            !owner && user && prod.length > 1 ?
              <Shipmentpage totalPrice={totalPrice} prod={prod} user={user} /> :
              !owner && user ?
                <Cartpage setTotalPrice={setTotalPrice} setProd={setProd} owner={owner} setOwner={setOwner} /> :
                <Homepage />
            }
          </Route> */}
        </Switch>
        <Route path="/login" exact component={!user ? Loginpage : Homepage} />
        <Footer />
      </Router >

      {showErrorIsOpen && <PopupMessage error={error} />}
    </div>
  );
}

export default App;
