import { Button } from '@material-ui/core';
import React, { useEffect } from 'react'
import { useRef } from 'react';
import { useState } from 'react';
import { Link, NavLink, useHistory, useLocation} from 'react-router-dom';
import styled from 'styled-components'
import logo from '../images/logo.png'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import HomeIcon from '@material-ui/icons/Home';
import InfoIcon from '@material-ui/icons/Info';
import LocalDiningIcon from '@material-ui/icons/LocalDining';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import MailIcon from '@material-ui/icons/Mail';
import TocIcon from '@material-ui/icons/Toc';
import CloseIcon from '@material-ui/icons/Close';
import Badge from '@material-ui/core/Badge';
import { useSelector } from 'react-redux';
import { logout,  selectCart,  selectUser } from '../features/userSlice';
import { useDispatch } from 'react-redux';
import { auth } from './firebase';
import { Avatar } from '@material-ui/core';

export default function Navbar({owner, setOwner}) {
    const [scrollNav, setScrollNav] = useState(window.innerWidth > 900 ? true : false);
    const navRef = useRef('');
    const location = useLocation();
    const user = useSelector(selectUser)
    const dispatch = useDispatch()
    const history = useHistory()
    const cart = useSelector(selectCart)
    

    const changeNav = () => {
        if (window.scrollY < 100 && window.innerWidth > 900) {
            setScrollNav(true)
        } else {
            setScrollNav(false)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', changeNav);
      
        return () => window.removeEventListener('scroll', changeNav);
    }, []);

    useEffect(() => {
        setScrollNav(window.innerWidth > 900 ? true : false)
        
        return
    }, [location])

    const handleLogout = () => {
        if (user) {
            auth.signOut().then(() => {
                dispatch(logout())
            })
            if (window.innerWidth < 900) {
                setScrollNav(false)
            }
        } else {
            history.push("/login")
            window.scrollTo(0, 0)
        }
        if (owner) {
            setOwner(false)
        }
    }


    return (
        <NavContainer ref={navRef} onMouseEnter={() => setScrollNav(true)}  scrollNav={scrollNav} user={user}>
            <ExpandMoreIcon onClick={() => setScrollNav(true)}/>

            <CloseIcon onClick={() => setScrollNav(false)} />

            <Link onClick={() => window.scrollTo(0, 0)} to=""><Logo src={logo} alt="" /></Link>

            <Ul>
                <Li><MyNavLink   onClick={() => window.scrollTo(0, 0)} activeClassName="active" exact={true} to="">Acasă
                    <HomeIcon/>
                    </MyNavLink>
                </Li>
                <Li><MyNavLink  onClick={() => window.scrollTo(0, 0)} activeClassName="active" to="despre">Despre
                    <InfoIcon/>
                    </MyNavLink>
                </Li>
                <Li><MyNavLink  onClick={() => window.scrollTo(0, 0)} activeClassName="active" to="contact">Contact
                    <MailIcon/>
                    </MyNavLink>
                </Li>
                <Li><MyNavLink  onClick={() => window.scrollTo(0, 0)} activeClassName="active" to="produse">Produse
                    <LocalDiningIcon/>
                    </MyNavLink>
                </Li>
                {
                    owner ? (
                        <Li><MyNavLink  onClick={() => window.scrollTo(0, 0)} activeClassName="active" to="depozit">Depozit
                                <TocIcon/>
                            </MyNavLink>
                        </Li>
                    ) : user ? (
                        <Li><MyNavLink  onClick={() => window.scrollTo(0, 0)} activeClassName="active" to="cos">Coș
                                <Badge badgeContent={cart.length} fontSize="small" color="secondary">
                                    <ShoppingCartIcon/>
                                </Badge>
                            </MyNavLink>
                        </Li>
                    ) : "" 
                }
                <Li style={{padding: '20px'}}>
                    <Avatar style={{verticalAlign: 'middle'}} src={user?.photoUrl} alt="" />
                    <i><small>{user?.displayName}</small></i>
                </Li>
            </Ul>

            <Button  onClick={handleLogout} variant="contained">{user ? 'Deconectare' : 'Autentificare'}</Button>
        </NavContainer>
    )
}







const NavContainer = styled.nav`
    background-color: ${({ scrollNav }) => (scrollNav ? 'whitesmoke' : '#00000094')};
    border-bottom: 1px solid ${({ scrollNav }) => (scrollNav ? 'var(--c1)' : 'transparent')};
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 69;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    width: ${({ scrollNav }) => (scrollNav ? '100%' : '100px')};
    height: ${({ scrollNav }) => (scrollNav ? '70px' : '30px')};
    padding: 0 20px;
    clip-path: ${({ scrollNav }) => (scrollNav ? 'polygon(0 0, 100% 0%, 100% 100%, 50% 100%, 0 100%)' : 'polygon(0 0, 100% 0%, 100% 0, 50% 100%, 0 0)')};
    transition: ${({scrollNav}) => (scrollNav ? 'clip-path 0.5s linear 0.5s, width 0.5s linear 0s, height 0.5s linear, background-color 0.5s linear' : 'clip-path 0.5s linear 0s, width 0.5s linear 0.5s,  height 0.5s linear, background-color 0.5s linear')};
    @media screen and (max-width: 900px) {
        height: ${({scrollNav}) => (scrollNav ? '100vh' : '30px')};
        flex-direction: column;
        justify-content: space-evenly;
    }
    >button {
        border-radius: 50px;
        width: 100px;
        height: 40px;
        font-size: 0.8rem;
        color: ${({user}) => (user ? '#3b3b3b' : 'white')};
        background-color: ${({user}) => (user ? '' : '#3b3b3b ')};
        text-transform: capitalize;
    }
    > ul, > a, > button {
        transform: ${({ scrollNav }) => (scrollNav ? 'translateY(0px)' : 'translateY(-100px)')};
        transition: transform 2s ease, opacity 1s linear;
        opacity: ${({ scrollNav }) => (scrollNav ? 1 : 0)};
    }
    >svg:first-of-type {
        position: absolute;
        color: whitesmoke;
        left: 50%;
        padding: 10px;
        box-sizing: content-box;
        z-index: 2;
        bottom: -12px;
        transform:  translateX(-50%);
        transition: all 1s ease;
        animation : blink 1s infinite;
        @keyframes blink {
            to {
                color: transparent;
            }
        }
        @media  screen and (min-width: 900px) {
            pointer-events: none;
        }
    }
    >svg:last-of-type {
        position: absolute;
        color: #3b3b3b;
        font-size: 2rem;
        right: 0;
        top: 0;
        padding: 10px;
        box-sizing: content-box;
        display: none;
        cursor: pointer;
        @media  screen and (max-width: 900px) {
            display: ${({scrollNav}) => (scrollNav ? 'flex' : 'none')}
        }
    }
`
const Ul = styled.ul`
    height: 100%;
    max-height: 300px;
    padding: 0;
    pointer-events: none;
    display: flex;
    align-items: center;
    text-align: center;
    @media screen and (max-width: 900px) {
        flex-direction: column;
    }
`
const Li = styled.li`
    height: 100%;
    list-style: none;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: var(--c1);
        bottom: 0px;
        left: -100%;
        z-index: 2;
        transition: .5s ease;
    }
    &:hover::after {
        left: 0;
    }
`
const MyNavLink = styled(NavLink)`
    pointer-events: all;
    display: flex;
    justify-self: center;
    align-items: center;
    height: 100%;
    padding: 0 10px;
    text-decoration: none;
    color: ${({scrollNav}) => (scrollNav ? 'transparent' : '#3b3b3b')};
    &.active {
        border-top: 3px solid transparent;
        border-bottom: 3px solid var(--c1);
    }
    >svg, >span svg {
        font-size: 1rem;
        vertical-align: top;
        margin-left: 2px;
    }
    span {
        /* overflow: unset; */
        margin-right: 5px;
    }
`
const Logo = styled.img`
    display: block;
    width: 100px;
    max-height: 70px;
    object-fit: contain;
`