import React from 'react'
import { useEffect } from 'react';
import styled from 'styled-components';
import { db } from './firebase';
import { useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import SwipeableViews from 'react-swipeable-views';
import EmailTab from './EmailTab'
import { useState } from 'react';

function a11yProps(index) {
    return {
      id: `scrollable-auto-tab-${index}`,
      'aria-controls': `scrollable-auto-tabpanel-${index}`,
    };
}

export default function EmailTabs() {
    const theme = useTheme();
    const [tabValue, setTabValue] = useState(0)
    const [items, setItems] = useState([])

    const handleChange = (event, newValue) => {
        setTabValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setTabValue(index);
    };

    useEffect(() => {
        db.collection('emails').orderBy('timestamp', 'desc').onSnapshot(snap => (
            setItems(snap.docs.map(doc => (
                {
                    id: doc.id,
                    data: doc.data()
                }
            )))
        ))
    }, [])

    const handleRead = (id, read) => {
        db.collection('emails').doc(id).update({
            read: !read 
        })
    }

    const handleDelete = (id) => {
        if (window.confirm(`Sigur ștergi acest mesaj?`) === true) {
            db.collection('emails').doc(id).delete()
        }
    }
    return (
        <Container>
            <Title>Mesaje:</Title>
             <AppBar position="static" color="default">
                <Tabs
                value={tabValue}
                onChange={handleChange}
                variant="scrollable"
                scrollButtons="on"
                aria-label="scrollable auto tabs example"
                >
                    {items && items.map(({id, data}, idx) => (
                        <MyTab className={data.read ? 'marked' : ''} key={id} label={data.name} {...a11yProps(idx)}/>
                    ))}
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={tabValue}
                onChangeIndex={handleChangeIndex}
            >
                {items && items.map(({id, data}, idx) => (
                    <EmailTab key={id} id={id} value={tabValue} index={idx} dir={theme.direction} data={data} handleRead={handleRead} handleDelete={handleDelete} />
                ))}
            </SwipeableViews>
        </Container>
    )
}


const Container = styled.div`
    margin: 100px auto 0;
    width: 1000px;
    max-width: 96vw;
    .MuiTab-textColorPrimary.Mui-selected {
        color: var(--c1);
    }
    .MuiTabs-indicator {
        background-color: var(--c1) !important;
    }
`
const Title = styled.h2`
    font-weight: 400;
    margin: 20px 0;
`
const MyTab = styled(Tab)`
    &.marked {
        text-decoration: line-through;
        color: lightgray;
    }
`