import React from 'react'
import { useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import SwipeableViews from 'react-swipeable-views';
import { useState } from 'react';
import TabPanel from './TabPanel';
import styled from 'styled-components';
import CartProduct from './CartProduct';
import FavProduct from './FavProduct';



function a11yProps(index) {
    return {
      id: `full-width-tab-${index}`,
      'aria-controls': `full-width-tabpanel-${index}`,
    };
  }

export default function CartFav({handleAmount, subtotal, cartItems, handleDelete, favItems, handleDeleteFav, handleAddToCart}) {
    const theme = useTheme();
    const [tabValue, setTabValue] = useState(0)

    const handleChange = (event, newValue) => {
        setTabValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setTabValue(index);
    };

    return (
        <Container>
            <AppBar position="static" color="default">
                <Tabs
                    value={tabValue}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    variant="fullWidth"
                >
                    <Tab label="Coșul de cumpărături" {...a11yProps(0)} />
                    <Tab label="Favorite" {...a11yProps(1)} />
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={tabValue}
                onChangeIndex={handleChangeIndex}
            >
                <TabPanel value={tabValue} index={0} dir={theme.direction}>
                    {cartItems && cartItems.length > 0 ? cartItems.map((item, id) => (
                        <CartProduct  key={id} item={item} index={id} handleDelete={handleDelete} handleAmount={handleAmount} subtotal={subtotal[id]}/>
                    )) :
                        <Text>Nu ai nici un produs in coșul de cumpărături</Text>
                    }
                </TabPanel>
                <TabPanel value={tabValue} index={1} dir={theme.direction}>
                    {favItems && favItems.length > 0 ? favItems.map((item, id) => (
                        <FavProduct key={id} item={item} handleDeleteFav={handleDeleteFav} handleAddToCart={handleAddToCart}/>
                    )) :
                        <Text>Nu ai nici un produs adăugat la favorite</Text>
                    }
                </TabPanel>
            </SwipeableViews>
        </Container>
    )
}



const Container = styled.div`
    margin: 5px;
    width: 100%;
    .MuiTab-textColorPrimary.Mui-selected {
        color: var(--c1);
    }
    .MuiTabs-indicator {
        background-color: var(--c1) !important;
    }
    @media screen and (max-width: 560px) {
        max-width: 97vw;
    }
`
const Text = styled.p`
    font-size: clamp(1.3rem, 5vw, 2rem);
`