import React from 'react'
import styled from 'styled-components'
import StarIcon from '@material-ui/icons/Star';
import StarBorderIcon from '@material-ui/icons/StarBorder';

export default function ReviewCard({img, name, description, fourStars}) {

    return (
        <Container>
            <Description>{description}</Description>
            <Wrapper>
                <Avatar src={img} alt=""/>
                <Name>{name}</Name>
            </Wrapper>
            <StarsWrapper>
                <StarIcon/>
                <StarIcon/>
                <StarIcon/>
                <StarIcon/>
                {fourStars ? <StarBorderIcon/> : <StarIcon/>}
            </StarsWrapper>
        </Container>
    )
}

const Container = styled.div`
    height: 400px;
    border: 1px solid grey;
    width: 300px;
    background-color: #000000c5;
    border-radius: 5px;
    padding: 20px;
    position: relative;
    box-shadow: 0 5px 10px #000000a4;
    animation: floating 5s infinite alternate linear;
    @keyframes floating {
        to {
            transform: translateY(-30px);
        }
    }
    &:nth-child(1) {
        animation-delay: 4s;
    }
    &:nth-child(3) {
        animation-delay: 2s;
    }
    @media screen and (max-width: 900px) {
        animation: none;
    }
`
const Description = styled.p`
    font-size: 0.9rem;
    color: white;
    position: relative;
    text-align: justify;
    &::after {
        content: '';
        position: absolute;
        bottom: -30px;
        right: -50px;
        width: 80%;
        height: 1px;
        background-color: #00000053;
    }
`
const Wrapper = styled.div`
    display: flex;
    gap: 30px;
    align-items: center;
    position: absolute;
    bottom: 30px;
    left: -50px;
`
const Avatar = styled.img`
    width: 100px;
    box-shadow: 0 2px 10px #00000067;
`
const Name = styled.p`
    color: white;
    font-family: 'Big Shoulders Display', cursive;
    font-size: 2rem;
`
const StarsWrapper = styled.div`
    position: absolute;
    bottom: 10px;
    right: 10px;
    >svg {
        color: #ecec00;
    }
`

