import React from 'react'
import styled from 'styled-components'
import arrow from '../images/left-arrow.svg'
import ReviewCard from './ReviewCard'
import avatar1 from '../images/avatar1.jpg'
import avatar2 from '../images/avatar2.jpg'
import avatar3 from '../images/avatar3.jpg'


export default function ReviewsSection() {
    const list = [
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Autem quasi nam a consequuntur dolorum. Ducimus inventore alias fuga repellendus doloribus, blanditiis omnis nulla impedit, voluptas libero voluptate, nobis ipsum magnam.",
        "Lorem ipsum dolor sit, amet consectetur adipisicing elit. Tempore nemo esse, laborum architecto aperiam minima? Optio vitae dolores minima est!",
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptate dignissimos iusto vero delectus aut voluptatum excepturi consectetur hic porro, repellat numquam debitis accusamus dolore voluptatibus iste ab assumenda odit ipsam sint, cum obcaecati sunt provident quo facere. Praesentium, nisi quos."
    ]

    return (
        <Container>
            <Arrow src={arrow} />

            <Title>Ce spun clienții noștri</Title>
            <Wrapper>
                <ReviewCard img={avatar1} name="Maria" description={list[0]}/>
                <ReviewCard img={avatar2} name="Mario" description={list[1]}/>
                <ReviewCard img={avatar3} name="Ana" description={list[2]} fourStars/>
            </Wrapper>
        </Container>
    )
}


const Container = styled.section`
    position: relative;
    overflow: hidden;
    background-image: linear-gradient(22.5deg, rgba(67, 67, 67, 0.02) 0%, rgba(67, 67, 67, 0.02) 29%,rgba(47, 47, 47, 0.02) 29%, rgba(47, 47, 47, 0.02) 37%,rgba(23, 23, 23, 0.02) 37%, rgba(23, 23, 23, 0.02) 55%,rgba(182, 182, 182, 0.02) 55%, rgba(182, 182, 182, 0.02) 69%,rgba(27, 27, 27, 0.02) 69%, rgba(27, 27, 27, 0.02) 71%,rgba(250, 250, 250, 0.02) 71%, rgba(250, 250, 250, 0.02) 100%),linear-gradient(67.5deg, rgba(117, 117, 117, 0.02) 0%, rgba(117, 117, 117, 0.02) 14%,rgba(199, 199, 199, 0.02) 14%, rgba(199, 199, 199, 0.02) 40%,rgba(33, 33, 33, 0.02) 40%, rgba(33, 33, 33, 0.02) 48%,rgba(135, 135, 135, 0.02) 48%, rgba(135, 135, 135, 0.02) 60%,rgba(148, 148, 148, 0.02) 60%, rgba(148, 148, 148, 0.02) 95%,rgba(53, 53, 53, 0.02) 95%, rgba(53, 53, 53, 0.02) 100%),linear-gradient(315deg, rgba(190, 190, 190, 0.02) 0%, rgba(190, 190, 190, 0.02) 6%,rgba(251, 251, 251, 0.02) 6%, rgba(251, 251, 251, 0.02) 18%,rgba(2, 2, 2, 0.02) 18%, rgba(2, 2, 2, 0.02) 27%,rgba(253, 253, 253, 0.02) 27%, rgba(253, 253, 253, 0.02) 49%,rgba(128, 128, 128, 0.02) 49%, rgba(128, 128, 128, 0.02) 76%,rgba(150, 150, 150, 0.02) 76%, rgba(150, 150, 150, 0.02) 100%),linear-gradient(270deg, #ffffff,#ffffff);
    padding: 0 30px 100px;
    border-bottom-right-radius: 20vw;
`
const Arrow = styled.img`
    position: absolute;
    top: 50px;
    right: -40px;
    width: 300px;
    max-width: 50%;
    transform: rotate(135deg);
    z-index: 0;
    opacity: 0.3;
`
const Title = styled.h1`
    margin: 100px 0 100px 50px;
    font-size: clamp(2rem, 5vw, 4rem);
    font-family: 'Big Shoulders Display', cursive;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        bottom: -30px;
        left: 0;
        transform: translateX(50%);
        width: 300px;
        height: 2px;
        background-color: #00000092;
    }
`
const Wrapper = styled.div`
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    flex-wrap: wrap;
    min-height: 600px;
`
