import React from 'react'
import styled from 'styled-components';
import ProductCard from './ProductCard';
import { useEffect } from 'react';
import { db } from './firebase'
import { useState } from 'react';
import ProductMain from './ProductMain';
import { selectError, showErrorIsTrue } from '../features/errorSlice';
import firebase from 'firebase'
import { intoCart, intoFav, selectAdmin, selectCart, selectFav, selectUser } from '../features/userSlice';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';

export default function ProductsSection({darkBg, type}) {
    const [items, setItems] = useState([])
    const [selected, setSelected] = useState()
    const user = useSelector(selectUser)
    const admin = useSelector(selectAdmin)
    const dispatch = useDispatch()
    const [inCart, setInCart] = useState(false)
    const [inFav, setInFav] = useState(false)
    const userDoc = user?.email
    const cart = useSelector(selectCart)
    const fav = useSelector(selectFav)


    useEffect(() => {
        let isMounted = true
        if (isMounted) {
            db.collection('knives').where('id', 'in', [`${type}`]).onSnapshot(snap => (
                setItems(snap.docs.map(doc => (
                    {
                        id: doc.id,
                        data: doc.data()
                    }
                )))
            )) 
        }
        return () => isMounted = false
    }, [type])

        
    useEffect(() => {
        let isMounted = true
        if (isMounted) {
            setSelected(items[0])
        }
        return () => isMounted = false
    }, [items])


    const handleAddToCart = (selected) => {
        if (ifOwnerNotUser()) return
       
        dispatch(intoCart([...cart, selected.data.name]))

        db.collection('users').doc(userDoc).collection('cart').doc(selected.data.name).set({
            name: selected.data.name,
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        })
        setInCart(true)
        dispatch(selectError(`Ai adăugat produsul ${selected.data.name} în coș✅`));
        dispatch(showErrorIsTrue())
    }

    const handleAddToFav = (selected) => {
        if (ifOwnerNotUser()) return

        dispatch(intoFav([...fav, selected.data.name]))

        db.collection('users').doc(userDoc).collection('fav').doc(selected.data.name).set({
            name: selected.data.name,
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        })
        setInFav(true)
        dispatch(selectError(`Ai adăugat produsul ${selected.data.name} la favorite✅`));
        dispatch(showErrorIsTrue())
    }

    function ifOwnerNotUser() {
        if (!user) {
            dispatch(selectError('Trebuie să te autentifici mai întâi❌'));
            dispatch(showErrorIsTrue())
            return true
        } else if (user?.email === admin) {
            dispatch(selectError('Proprietarul nu poate adăuga in coș/favorite❌'));
            dispatch(showErrorIsTrue())
            return true
        } 
    }
    

    const toSelected = {
        selected,
        darkBg,
        handleAddToCart,
        handleAddToFav,
        inCart,
        inFav,
        setInFav, 
        setInCart,
        fav,
        cart
    }
 

    return (
        <Container darkBg={darkBg}>
            <Wrapper>
                {selected && <ProductMain {...toSelected}/>}
            

                <BottomSide>
                    {items && items.map((item, i) => (
                        <ProductCard key={i} setSelected={setSelected} item={item}/> 
                    ))}
                </BottomSide>
            </Wrapper>
        </Container>
    )
}




const Container = styled.section`
    display: grid;
    place-content: center;
    background: ${({darkBg}) => (darkBg  ? 'linear-gradient(66deg, transparent 0%, transparent 24%,rgba(195, 195, 195,0.04) 24%, rgba(195, 195, 195,0.04) 80%,transparent 80%, transparent 100%),linear-gradient(153deg, transparent 0%, transparent 29%,rgba(195, 195, 195,0.04) 29%, rgba(195, 195, 195,0.04) 45%,transparent 45%, transparent 100%),linear-gradient(199deg, transparent 0%, transparent 32%,rgba(195, 195, 195,0.04) 32%, rgba(195, 195, 195,0.04) 59%,transparent 59%, transparent 100%),linear-gradient(277deg, transparent 0%, transparent 56%,rgba(195, 195, 195,0.04) 56%, rgba(195, 195, 195,0.04) 57%,transparent 57%, transparent 100%),linear-gradient(90deg, rgb(0,0,0),rgb(0,0,0))' : '')};
`
const Wrapper = styled.div`
    width: 1000px;
    max-width: 95vw;
    margin: 10vw auto;
`
const BottomSide = styled.div`
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    grid-auto-rows: 200px;
    gap: 10px;
    margin-bottom: 30px;
`
