import React from 'react'
import styled from 'styled-components'

export default function WorkerCard({img, name, title}) {

    return (
        <Container>
            <Avatar src={img} alt="" />
            <Name>{name}</Name>
            <Title>{title}</Title>
        </Container>
    )
}


const Container = styled.section`
    flex: 1 1 300px;
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family:  'Segoe UI', 'Roboto', 'Oxygen', sans-serif;
`
const Avatar = styled.img`
    object-fit: contain;
    border-radius: 50%;
    width: 150px;
    height: 150px;
    box-shadow: 0 5px 10px black;
    margin-bottom: 20px;
`
const Name = styled.p`
    font-weight: bold;
`
const Title = styled.small`

`
