import React from 'react'
import { Button, TextField } from '@material-ui/core'
import { Grid } from '@material-ui/core'
import { useDispatch } from 'react-redux'
import { useRef } from 'react'
import { useState } from 'react'
import AddIcon from '@material-ui/icons/Add';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import { InputLabel } from '@material-ui/core'
import { FormControl } from '@material-ui/core'
import { Select } from '@material-ui/core'
import { MenuItem } from '@material-ui/core'
import { Fab } from '@material-ui/core'
import {Container as Contain} from '@material-ui/core';
import { db } from './firebase'
import { useEffect } from 'react'
import { selectError, showErrorIsTrue } from '../features/errorSlice'
import useStorage from './useStorage'
import firebase from 'firebase';
import ProductTable from './ProductTable'
import { motion } from 'framer-motion'
import styled from 'styled-components'


export default function ProductForm() {
    // for reseting table page back to 1 after product has been added to db
    const [added, setAdded] = useState(false)

    const [color, setColor] = useState('')
    const [size, setSize] = useState('')
    const [material, setMaterial] = useState('')
    const [handler, setHandler] = useState('')
    const [picture, setPicture] = useState('')
    const [name, setName] = useState('')
    const [id, setId] = useState('')
    const [price, setPrice] = useState('')
    const [stock, setStock] = useState('')
    const [description, setDescription] = useState('')
    const dispatch = useDispatch()
    const labelRef = useRef()
    const fileRef = useRef()
    const [total, setTotal] = useState(0)


    const handleColor = (e) => {
        setColor(e.target.value)
    }
    const handleSize = (e) => {
        setSize(e.target.value)
    }
    const handleMaterial = (e) => {
        setMaterial(e.target.value)
    }
    const handleHandler = (e) => {
        setHandler(e.target.value)
    }

    const {picUrl, progress} = useStorage(picture)

    useEffect(() => {
        if(picUrl) {
            setPicture('')
        }
        return;
    }, [picUrl, setPicture])

    const types = [
        "image/apng",
        "image/bmp",
        "image/gif",
        "image/jpeg",
        "image/jpg",
        "image/pjpeg",
        "image/png",
        "image/tiff",
        "image/webp"
    ];

    const handlePicture = e => {
        let img = e.target.files[0]
        labelRef.current.classList.remove('bounce')

        if (img && types.includes(img.type) && img.size < 2000000) {
            setPicture(img);
            dispatch(selectError(`Ai selectat imaginea ${img.name} ✅`));
            dispatch(showErrorIsTrue())
        } else  {
            setPicture('');
            dispatch(selectError('Imaginea selectată nu este validă sau mai mare de 2mb ❌'));
            dispatch(showErrorIsTrue())
        }
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        if (total >= 100) {
            dispatch(selectError('Ai atins limita maximă de 100 de produse❌'))
            dispatch(showErrorIsTrue())
            return
        }

        if (fileRef.current.value === '') {
            dispatch(selectError('Alege o imagine 🖼'));
            dispatch(showErrorIsTrue())
            labelRef.current.classList.add('bounce')
            return
        }

        db.collection('knives').add({
            name: name,
            id: id,
            image: picUrl || '',
            price: Number(price),
            stock: Number(stock),
            option1: color,
            option2: size,
            option3: material,
            option4: handler,
            description: description,
            rating: null,
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        })
        
        setColor('')
        setSize('')
        setMaterial('')
        setHandler('')
        setPicture('')
        setName('')
        setId('')
        setPrice('')
        setStock('')
        setDescription('')

        setAdded(true)
    }



    return (
        <Container>
            
            <FormWrapper  onSubmit={handleSubmit}>
                <Title>Adaugă produs:</Title>
                    {picture && <ProgressContainer 
                                    as={motion.div} 
                                    initial={{width: 0}}
                                    animate={{width: (!progress ? 0 : progress) + '%'}}
                                />
                    }
                    <TextField 
                        inputProps={{ maxLength: 50 }}
                        required 
                        label="Nume Produs" 
                        fullWidth
                        value={name}
                        onChange={e => setName(e.target.value)}
                    />
                    
                    <Grid container spacing={1}>
                        <Grid item xs={10} sm={11}>
                            <TextField 
                            label="ID" 
                            inputProps={{ maxLength: 20 }}
                            required
                            fullWidth
                            value={id}
                            onChange={e => setId(e.target.value)}
                        />
                        </Grid>
                        <Grid item xs={2} sm={1}>
                            <Contain maxWidth="xs" title="Adaugă imagine">
                                <Fab size="small" color="primary" aria-label="add" ref={labelRef}
                                    style={{width: 40, height: 40, padding: 0, marginLeft: -20, marginBottom: -60, position: 'relative', zIndex: 1}} >
                                    <Label>
                                        <InputFile ref={fileRef} type="file" onChange={handlePicture}/>
                                        <AddPhotoAlternateIcon />
                                    </Label>
                                </Fab>
                            </Contain>
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="Preț"
                                type="number"
                                fullWidth
                                required
                                value={price}
                                onChange={e => setPrice(e.target.value)}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="Stoc"
                                type="number"
                                fullWidth
                                value={stock}
                                onChange={e => setStock(e.target.value)}
                            />
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth >
                                <InputLabel id="culoare">Culoare</InputLabel>
                                <Select
                                    labelId="culoare"
                                    value={color}
                                    onChange={handleColor}
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="rosu">Roșu</MenuItem>
                                    <MenuItem value="negru">Negru</MenuItem>
                                    <MenuItem value="alb">Alb</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth>
                                <InputLabel id="size">Mărime</InputLabel>
                                <Select
                                    labelId="size"
                                    value={size}
                                    onChange={handleSize}
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="mic">Mic</MenuItem>
                                    <MenuItem value="medius">Mediu</MenuItem>
                                    <MenuItem value="mare">Mare</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth>
                                <InputLabel id="material">Material</InputLabel>
                                <Select
                                    labelId="material"
                                    value={material}
                                    onChange={handleMaterial}
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="titaniu">Titaniu</MenuItem>
                                    <MenuItem value="carbon">Fibră de carbon</MenuItem>
                                    <MenuItem value="damasc">Damasc</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth>
                                <InputLabel id="handler">Lemn mâner</InputLabel>
                                <Select
                                    labelId="handler"
                                    value={handler}
                                    onChange={handleHandler}
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="nuc">Nuc</MenuItem>
                                    <MenuItem value="maslin">Măslin</MenuItem>
                                    <MenuItem value="brad">Brad</MenuItem>
                                    <MenuItem value="cires">Cireș</MenuItem>
                                    <MenuItem value="fag">Fag</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                    </Grid>

                    <TextField
                        label="Descriere"
                        inputProps={{ maxLength: 1000 }}
                        value={description}
                        onChange={e => setDescription(e.target.value)}
                        multiline
                        rows={6}
                        margin="dense"
                    />
                    <Info><i>{description.length}</i>/1000</Info>

                    <Button
                        type="submit"
                        variant="contained"
                        color="default"
                        endIcon={<AddIcon />}
                    >
                        Adaugă produs
                    </Button>
                </FormWrapper>

                <ProductTable  setTotal={setTotal} added={added} setAdded={setAdded}/>
        </Container>
    )
}

const Container = styled.div`
    .bounce {
        animation: bounce 1s both;
        border: 2px solid red;
        @keyframes bounce { 
            0%, 20%, 50%, 80%, 100% {
                transform: translateY(0);
            } 
            40% {
                transform: translateY(-30px);
            } 
            60% {
                transform: translateY(-15px);
            } 
         }
    }
`
const Title = styled.h2`
    font-weight: 400;
    margin: 20px 0;
`
const FormWrapper = styled.form`
    background-color: #ffffff9f;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    margin: 100px auto 0;
    width: 1000px;
    max-width: 96vw;
    flex: 1;
    padding: 20px;
    position:relative;
    button {
        width: 200px;
        height: 40px;
    }
`
const Label = styled.label`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    >svg {
        margin-top: 7px;
        margin-left: 2px;
    }
`
const InputFile = styled.input`
    height: 0;
    width: 0;
    opacity: 0;
`
const ProgressContainer = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    height: 5px;
    width: 0;
    background-color: var(--c1);
`
const Info = styled.small`
    color: grey;
`