import React from 'react'
import { useRef } from 'react'
import { useEffect } from 'react'
import styled from 'styled-components'
import img2 from '../images/img2.jpg'
import img1 from '../images/img1.jpg'

export default function AboutSection() {
    const textRef = useRef()
    const scrollTextRef = useRef()
    const img2Ref = useRef()
    const img1Ref = useRef()
    
    useEffect(() => {
        function handleScroll(){
            textRef.current.style.transform = `translateY(${(textRef.current.offsetTop - 600) - window.scrollY / 3}px)`
            if (scrollTextRef.current.getBoundingClientRect().y < window.innerHeight / 1.1) {
                scrollTextRef.current.style.transform = `translateX(${window.scrollY}px)`
            } 
            if (window.scrollY + window.innerHeight - 1500 > img2Ref.current.offsetTop) {
                img2Ref.current.style.objectPosition = `bottom center`
            }
            if (window.scrollY + window.innerHeight  > img1Ref.current.offsetTop) {
                img1Ref.current.style.clipPath = 'polygon(100% 0, 0 0, 0 100%, 100% 100%)'
            } 
        }

        window.addEventListener('scroll', handleScroll)
        
        return () => window.removeEventListener('scroll', handleScroll)
        
    }, [])


    return (
        <Container>
            <SlidingText>SATÂR&nbsp; SPATULĂ&nbsp; BRICEAG&nbsp; FELIERE&nbsp; DEZOSAT&nbsp; DECOJIT&nbsp; PÂINE&nbsp; TOCARE&nbsp; DECORATIV</SlidingText>

            <Decoration1/>

            <Wrapper1 ref={img1Ref}>
                <Img1  src={img1} alt='' />
            </Wrapper1>

            <P ref={textRef}>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Ut esse obcaecati aperiam voluptatibus adipisci iste illum repellat, nihil, excepturi placeat doloribus facilis veniam eum maiores? Beatae harum reiciendis possimus, placeat.</P>

            <Decoration2/>

            <Wrapper2>
                <Img2 ref={img2Ref} src={img2} alt='' />
            </Wrapper2>

            <ScrollingText ref={scrollTextRef}>Bohler-M390 N690 CPM-154 S35VN S30VN S30V S90V VG-10</ScrollingText>
        </Container>
    )
}





const Container = styled.section`
    height: 200vh;
    position: relative;
    overflow-x: hidden;
    display: grid;
    place-items: center;
    background-image: linear-gradient(22.5deg, rgba(67, 67, 67, 0.02) 0%, rgba(67, 67, 67, 0.02) 29%,rgba(47, 47, 47, 0.02) 29%, rgba(47, 47, 47, 0.02) 37%,rgba(23, 23, 23, 0.02) 37%, rgba(23, 23, 23, 0.02) 55%,rgba(182, 182, 182, 0.02) 55%, rgba(182, 182, 182, 0.02) 69%,rgba(27, 27, 27, 0.02) 69%, rgba(27, 27, 27, 0.02) 71%,rgba(250, 250, 250, 0.02) 71%, rgba(250, 250, 250, 0.02) 100%),linear-gradient(67.5deg, rgba(117, 117, 117, 0.02) 0%, rgba(117, 117, 117, 0.02) 14%,rgba(199, 199, 199, 0.02) 14%, rgba(199, 199, 199, 0.02) 40%,rgba(33, 33, 33, 0.02) 40%, rgba(33, 33, 33, 0.02) 48%,rgba(135, 135, 135, 0.02) 48%, rgba(135, 135, 135, 0.02) 60%,rgba(148, 148, 148, 0.02) 60%, rgba(148, 148, 148, 0.02) 95%,rgba(53, 53, 53, 0.02) 95%, rgba(53, 53, 53, 0.02) 100%),linear-gradient(315deg, rgba(190, 190, 190, 0.02) 0%, rgba(190, 190, 190, 0.02) 6%,rgba(251, 251, 251, 0.02) 6%, rgba(251, 251, 251, 0.02) 18%,rgba(2, 2, 2, 0.02) 18%, rgba(2, 2, 2, 0.02) 27%,rgba(253, 253, 253, 0.02) 27%, rgba(253, 253, 253, 0.02) 49%,rgba(128, 128, 128, 0.02) 49%, rgba(128, 128, 128, 0.02) 76%,rgba(150, 150, 150, 0.02) 76%, rgba(150, 150, 150, 0.02) 100%),linear-gradient(270deg, #ffffff,#ffffff);
`
const SlidingText = styled.p`
    position: absolute;
    z-index: 10;
    top: 0;
    left: 10%;
    color: transparent;
    -webkit-text-stroke: 3px #5c5c5c;
    font-size: clamp(3rem, 10vw, 10rem);
    width: max-content;
    animation: scrollX 100s infinite alternate linear;
    @keyframes scrollX {
        to {
            transform: translateX(-80%);
        }
    }
    @media screen and (max-width: 900px) {
        -webkit-text-stroke: 1px  black;
    }
`
const Decoration1 = styled.div`
    position: absolute;
    top: 20%;
    right: 20%;
    width: 60vh;
    height: 10vh;
    z-index: 2;
    background-color: #00000047;
`
const Decoration2 = styled.div`
    position: absolute;
    top: 70%;
    left: 10%;
    width: 20vw;
    height: 40vh;
    z-index: 2;
    background-color: #61292976;
`
const Wrapper1 = styled.div`
    width: 600px;
    height: 400px;
    max-width: 70vw;
    max-height: 60vw;
    position: absolute;
    top: 10%;
    right: 0;
    clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
    transition: all 2s linear;
    `
const Img1 = styled.img`
    object-fit: cover;
    width: 100%;
    height: 100%;
   
`
const P = styled.p`
    text-align: justify;
    width: 1000px;
    max-width: 90vw;
    font-size: clamp(0.9rem, 3vw, 2rem);
    transition: all 0.5s ease-out;
    position: relative;
    z-index: 1;
    &::after {
        content: '';
        position: absolute;
        bottom: -20px;
        width: 20%;
        right: 0;
        height: 2px;
        background-color: var(--c1);
        animation: underln 6s infinite alternate linear;
        @keyframes underln {
            30% {
                transform: translateX(20%);
                width: 50%;
            }
            100% {
                transform: translateX(20%);
                width: 50%;
            }
        }
    }
`
const Wrapper2 = styled.div`
    position: absolute;
    bottom: 20%;
    z-index: 0;
    left: 0;
    width: 1000px;
    height: 600px;
    max-height: 80vw;
    max-width: 70vw;
`
const Img2 = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 5s ease;
    object-position: top center;
`
const ScrollingText = styled.p`
    position: absolute;
    bottom: 0;
    right: 2300px;
    font-size: clamp(2rem, 5vw, 5rem);
    width: max-content;
    transition: all 0.5s ease-out;
    color: whitesmoke;
    filter: drop-shadow(.05em .05em var(--c1));
    @media screen and (max-width: 500px) {
        right: 1800px;
    } 
`
