import { TextField } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

export default function PaymentForm({user, setBilling}) {
    const [name, setName] = useState('')
    const [lname, setLname] = useState('')
    const [phone, setPhone] = useState('')
    const [email] = useState(user?.email)
    const [address, setAddress] = useState('')
    const [zip, setZip] = useState('')
    const [city, setCity] = useState('')
    const [county, setCounty] = useState('')

    useEffect(() => {
        setBilling({
            name,
            lname,
            phone,
            email,
            address,
            zip,
            city,
            county,
        })
    }, [name, lname, phone, address, zip, city, county, email, setBilling])
       

    return (
        <Container>
            <TextField
                label="Adresa de e-mail"
                type="email"
                name="e-mail"
                fullWidth
                margin="dense"
                color="secondary"
                value={email}
                disabled
            />
            <TextField
                label="Nume"
                required
                type="text"
                name="lname"
                fullWidth
                color="secondary"
                margin="dense"
                value={lname}
                onChange={(e) => setLname(e.target.value)}
                />
            <TextField
                label="Prenume"
                required
                margin="dense"
                type="text"
                name="fname"
                fullWidth
                color="secondary"
                value={name}
                onChange={(e) => setName(e.target.value)}
            />
            <TextField
                label="Număr de Telefon"
                required
                margin="dense"
                type="tel"
                name="phone"
                fullWidth
                color="secondary"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
            />
            <TextField
                label="Adresa"
                required
                type="text"
                name="address"
                fullWidth
                margin="dense"
                color="secondary"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
            />
            <TextField
                label="Cod Poștal"
                required
                type="number"
                name="zip code"
                fullWidth
                margin="dense"
                color="secondary"
                value={zip}
                onChange={(e) => setZip(e.target.value)}
            />
            <TextField
                label="Localitatea"
                required
                type="text"
                name="city"
                fullWidth
                margin="dense"
                color="secondary"
                value={city}
                onChange={(e) => setCity(e.target.value)}
            />
            <TextField
                label="Județul"
                required
                type="text"
                name="county"
                fullWidth
                margin="dense"
                color="secondary"
                value={county}
                onChange={(e) => setCounty(e.target.value)}
            />
        </Container>
    )
}


const Container = styled.div`

`