import React from 'react'
import styled from 'styled-components'

export default function KnifeTypes({id, title, active, setSelected}) {
    return (
        <Container onClick={() => setSelected(id)} className={active ? 'active' : ""}>
            {title}
        </Container>
    )
}

const Container = styled.li`
    list-style: none;
    font-size: 0.9rem;
    margin: 0 25px;
    padding: 7px;
    border-radius: 6px;
    cursor: pointer;
    &.active, &:hover {
        background-color: var(--c1h);
        color: white;
    }
`
