import { createSlice } from '@reduxjs/toolkit';


export const userSlice = createSlice({
  name: 'user',
  initialState: {
    user: null,
    cart: [],
    fav: [],
    owner: 'codmetwo@gmail.com',
  },
  reducers: {
    login: (state, action) => {
      state.user = action.payload;
    },
    logout: (state) => {
      state.user = null;
    },
    intoCart: (state, action) => {
      state.cart = action.payload
    },
    intoFav: (state, action) => {
      state.fav = action.payload
    },
  },
});

export const { login, logout, intoCart, intoFav } = userSlice.actions;

export const selectUser = (state) => state.user.user;

export const selectCart = (state) => state.user.cart;

export const selectFav = (state) => state.user.fav;

export const selectAdmin = (state) => state.user.owner;

export default userSlice.reducer;
